  .headerSecondary {
    height: 40px;
    display: flex;
    align-items: center;
    background-color: #232f3e;
    position: sticky;
    top: 0;
    z-index: 100;
  }
  
  .headerSecondary_optionLineTwo {
    font-size: 14px;
    font-weight: 700;
    margin-top: 5px;
  }
  
  .headerSecondary_optionLast {
    font-size: 14px;
    font-weight: 700;
    margin-top: 5px;
    margin-left: 70px;
  }
  
  .header__logo {
    width: 100px;
    object-fit: contain;
    margin: 0 20px;
    margin-top: 18px;
  }
  
  .header__search {
    display: flex;
    flex: 1;
    align-items: center;
    border-radius: 24px;
  }
  
  .header__searchInput {
    height: 12px;
    padding: 10px;
    border: none;
    width: 100%;
  }
  
  .header__searchIcon {
    padding: 5px;
    height: 22px !important;
    background-color: #cd9042;
  }
  
  .header_optionLineOne {
    font-size: 10px;
  }
  
  .header_optionLineTwo {
    font-size: 14px;
    font-weight: 700;
  }
  
  .header__optionBasket {
    display: flex;
    align-items: center;
    color: white;
  }
  
  .header__basketCount {
    margin-right: 10px;
    margin-left: 10px;
  }
  
  .header__nav {
    display: flex;
    justify-content: space-evenly;
  }
  
  .header__option {
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
    color: white;
  }