.productLists {
    display: flex;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    /* overflow-y: hidden; */
    max-width: 1300px;
  }
  
  .home__row {
    display: flex;
    z-index: 1;
    margin-left: 5px;
    margin-right: 5px;
  }
  
  .home__image {
    width: 100%;
    z-index: -1;
    margin-bottom: -150px;
    mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
  }
  
  .productLists__row {
    background-color: white;
    margin: 30px;
    overflow-x: hidden;
  }
  
  .productLists__button {
    display: block;
    box-sizing: border-box;
    margin: 0 auto;
    padding: 8px;
    width: 80%;
    max-width: 200px;
    background: #fff; /* fallback color for old browsers */
    background: rgba(255, 255, 255, 0.5);
    border-radius: 8px;
    color: #000000;
    text-align: center;
    text-decoration: none;
    letter-spacing: 1px;
    transition: all 0.3s ease-out;
    cursor: pointer;
    height: 50%;
    border-color: white;
  }
  
  .productLists__button:hover {
    background-color: rgb(224, 224, 224);
    border: 1px solid white;
    color: white;
  }