.register {
    background-color: white;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .register__logo {
    margin-top: 20px;
    margin-bottom: 20px;
    object-fit: contain;
    width: 100px;
    margin-right: auto;
    margin-left: auto;
  }
  
  .register__container {
    width: 300px;
    height: fit-content;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    border: 1px solid lightgray;
    padding: 20px;
  }
  
  .register__container > h1 {
    font-weight: 500;
    margin-bottom: 20px;
  }
  
  .register__container > form > h5 {
    margin-bottom: 5px;
  }
  
  .register__container > form > input {
    height: 30px;
    margin-bottom: 10px;
    background-color: white;
    width: 98%;
  }
  
  .register__container > p {
    margin-top: 15px;
    font-size: 12px;
  }
  
  .register__signIn {
    padding-top: 35px;
  }
  
  hr {
    border: none;
    height: 20px;
    width: 90%;
    height: 50px;
    margin-top: 0;
    box-shadow: 0 15px 10px -20px #333;
    margin: -50px auto 20px;
  }
  
  .register__createButton {
    cursor: pointer;
    background: #f0c14b;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
  }
  
  .register__createButton:hover {
    background: #f0b623;
  }